import React from "react";
import { Col, Divider, Row } from "antd";
import NYD from "./NYD";
import Orion from "./Orion";
import Carigos from "./Carigos";
import SiftandPick from "./SiftandPick";
import OCBC from "./OCBC";

import { Container } from "../styled";
import { Work } from "./styled";

const Jobs = [SiftandPick, OCBC, Carigos, Orion, NYD];
const Job = (props: {
  last: boolean;
  title: string;
  position: string[];
  year: string;
  description: JSX.Element;
}) => {
  let columnWide = {
    left: 20,
    right: 4
  };
  if (props.year.length > 9) {
    columnWide = {
      left: 18,
      right: 6
    };
  }
  let pageBreak = props.last ? "" : "page-break";
  return (
    <Work className={`jobs ${pageBreak}`}>
      <Row>
        <Col xs={24} sm={columnWide.left}>
          <h3 className="company">{props.title}</h3>
          {props.position.map((position, index) => (
            <h4 key={index} className="position">
              {position}
            </h4>
          ))}
        </Col>
        <Col xs={24} sm={columnWide.right}>
          <div className="year">{props.year}</div>
        </Col>
      </Row>
      <div className="description">{props.description}</div>
      {!props.last && <Divider />}
    </Work>
  );
};
export default () => {
  const totalWork = Jobs.length - 1;
  return (
    <Container>
      <h2 className="ui header">Working Experience</h2>
      <div className="content">
        {Jobs.map((job: any, index: number) => (
          <Job
            key={index}
            last={index === totalWork}
            title={job.title}
            year={job.year}
            position={job.position}
            description={job.description}
          />
        ))}
      </div>
    </Container>
  );
};
